.experience {
  margin: 0 auto;
  padding: 80px 0;
  background-color: #b8dde1;
  color: #006f62;
  .subSectionTitle {
    font-size: 28px;
    font-weight: 700;
    margin: 30px 0 20px;
  }
  .educationRow {
    width: 80%;
    margin: auto 10%;
    padding: 20px 0;
    border-bottom: 1px solid rgba(100, 100, 100, 0.5);
    font-size: 24px;
    &:last-child {
      border-bottom: none;
    }
    .school {
      font-weight: bold;
    }
    .locationIcon {
      display: inline-block;
      margin-right: 10px;
    }
    .educationSection {
      text-align: right;
    }
  }
}
