@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/3.3.6/css/bootstrap.min.css);
@import url(https://use.fontawesome.com/74dfc6cf47.css);
.Banner{height:100vh;background:url("/static/img/bannerBg.JPG") no-repeat center bottom fixed;background-size:cover}.Banner .BannerContainer{text-align:center;max-width:1440px;margin:0 auto;position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;z-index:2;height:100%;color:#ffffff}.Banner .myName{font-size:63px;font-weight:500;padding:20px;border-bottom:1px solid rgba(255,255,255,0.5);letter-spacing:1px}.Banner .welcome{font-size:50px;font-weight:500;text-transform:uppercase;letter-spacing:1px}.Banner .down-arrow{position:absolute;bottom:40px;left:calc(50% - 15px);z-index:2}.Banner .down-arrow img{width:30px;cursor:pointer}.Banner .BannerOverlay{position:absolute;top:0;left:0;right:0;bottom:0;background:#17429E;opacity:0.75;z-index:1}

.profile{margin:0 auto;padding:80px 0;color:#333333}.profile .sectionTitle{color:#17429E}.profile .detailSection{text-align:left}.profile .detailSection .subSectionTitle{font-size:28px;font-weight:bold;color:#17429E;margin-bottom:20px}.profile .detailSection .subSectionLabel{color:#333333;font-weight:bold;font-size:18px}.profile .detailSection .subSectionValue{font-size:18px;color:#555555;margin-bottom:5px}.profile .profileAvatar{width:80%;border-radius:50%;border:10px solid #e3e3e3}

.experience{margin:0 auto;padding:80px 0;background-color:#b8dde1;color:#006f62}.experience .subSectionTitle{font-size:28px;font-weight:700;margin:30px 0 20px}.experience .educationRow{width:80%;margin:auto 10%;padding:20px 0;border-bottom:1px solid rgba(100,100,100,0.5);font-size:24px}.experience .educationRow:last-child{border-bottom:none}.experience .educationRow .school{font-weight:bold}.experience .educationRow .locationIcon{display:inline-block;margin-right:10px}.experience .educationRow .educationSection{text-align:right}

.contacts{margin:0 auto;padding:80px 0;background-color:#434242;color:#ffffff}.contacts .sectionSubTitle{opacity:0.5}.contacts .socialLinks{display:block;color:#ffffff;margin:10px 0;font-size:18px;line-height:40px}.contacts .socialLinks:hover{color:rgba(255,255,255,0.63);text-decoration:none;-webkit-transform:translateX(10px);transform:translateX(10px)}.contacts .socialLinks:hover .icon{color:#333333;background-color:rgba(255,255,255,0.3)}.contacts .socialLinks:focus{color:#fff;text-decoration:none;outline:none}.contacts .address{display:inline-block;vertical-align:middle;margin-left:10px}.contacts .icon{background:rgba(255,255,255,0.63);text-align:center;color:#ffffff;font-size:20px;width:40px;height:40px;display:inline-block;border-radius:6px}.contacts .detailSection{text-align:left}.contacts .detailSection .subSectionTitle{font-size:28px;font-weight:bold;color:#ffffff;margin-bottom:20px}.contacts .detailSection .subSectionLabel{color:#ffffff;font-weight:bold;font-size:18px}.contacts .detailSection .subSectionValue{font-size:18px;color:#ffffff;margin-bottom:5px}

*{box-sizing:border-box}body,html{width:100%;margin:0;padding:0;text-align:center;font-family:'Raleway', sans-serif}a{text-decoration:none}.maxWidthContainer{width:100%;max-width:1280px;margin:0 auto;padding:0 40px}.sectionTitle{font-size:52px;font-weight:600;margin-bottom:10px}.sectionSubTitle{font-size:24px;font-weight:500;text-transform:uppercase;border-bottom:1px solid #e3e3e3;padding-bottom:30px;margin-bottom:50px}.isDesktopOnly{display:block}.isMobileOnly{display:none}@media screen and (max-width: 768px){.isDesktopOnly{display:none !important}.isMobileOnly{display:block !important}}

