.contacts{
  margin: 0 auto;
  padding: 80px 0;
  background-color: #434242;
  color: #ffffff;
  .sectionSubTitle{
    opacity: 0.5;
  }
  .socialLinks{
    display: block;
    color: #ffffff;
    margin: 10px 0;
    font-size: 18px;
    line-height: 40px;
  }
  .socialLinks:hover{
    color: rgba(255, 255, 255, 0.63);
    text-decoration: none;
    transform: translateX(10px);
  }
  .socialLinks:hover .icon{
    color: #333333;
    background-color: rgba(255, 255, 255, 0.3);
  }
  .socialLinks:focus{
    color: #fff;
    text-decoration: none;
    outline: none;
  }
  .address{
    display: inline-block;
    vertical-align: middle;
    margin-left: 10px;
  }
  .icon {
    background: rgba(255, 255, 255, 0.63);
    text-align: center;
    color: #ffffff;
    font-size: 20px;
    width: 40px;
    height: 40px;
    display: inline-block;
    border-radius: 6px;
  }
  .detailSection{
    text-align: left;
  }
  .detailSection .subSectionTitle{
    font-size: 28px;
    font-weight: bold;
    color: #ffffff;
    margin-bottom: 20px;
  }
  .detailSection .subSectionLabel{
    color: #ffffff;
    font-weight: bold;
    font-size: 18px;
  }
  .detailSection .subSectionValue{
    font-size: 18px;
    color: #ffffff;
    margin-bottom: 5px;
  }
}
